@mixin onDesktop {
  @media (min-width: 1199px) {
    @content;
  }
}

@mixin onTablet {
  @media (max-width: 1199px) {
    @content;
  }
}

@mixin onLowTablet {
  @media (max-width: 768px){
    @content;
  }
}

@mixin onPhone {
  @media (max-width: 639px) {
    @content;
  }
}

@mixin onLowPhone {
  @media (max-width: 639px) {
    @content;
  }
}

@mixin query ($cols) {
  grid-template-columns: repeat($cols, 1fr);
}